.nav {
  position: fixed;
  display: flex;
  flex-direction: column;
  left: 30px;
  top: 50%;
  gap: 25px;
  z-index: 2;
  transform: translateY(-50%);
  overflow: hidden;
  &__button {
    width: 30px;
    height: 3px;
    background: var(--primary);
    transform: translateX(-150%);
    animation: navButton 1s 0.5s cubic-bezier(0, 0.45, 0.09, 0.99) forwards;
  }
}

@keyframes navButton {
  to {
    transform: translateX(0%);
  }
}

.current {
  width: 50px;
}
