.curtain {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  background: var(--secondary);
  box-shadow: var(--box-shadow);
  animation: curtain 1s ease-in-out forwards;
  transition: 2s;
}

@keyframes curtain {
  from {
    transform: translatey(0);
  }
  to {
    transform: translateY(-102vh);
  }
}
