.contact {
  &__socials {
    display: flex;
    align-items: center;
    margin: 15px 0 0 0;
    gap: 15px;
    transform: translateX(-100%);
    transition: var(--long-transition) var(--cubic-transition);
    transition-delay: var(--basic-transition);
    overflow: hidden;
    &__link {
      height: 50px;
      width: 50px;
    }
  }
  &__image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transform: scale(1.07);
    transition: 0.1s;
    &__container {
      max-width: 700px;
      width: 100%;
      height: 400px;
      margin: 0 5px 0 0;
      border-radius: 10px;
      box-shadow: var(--box-shadow);
      transform: translatex(15%) scale(0.9);
      transition: var(--long-transition);
      overflow: hidden;
    }
  }
}
