.details {
  &__inner {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    padding: 100px 50px;
  }
  &__button {
    position: absolute;
    bottom: 0px;
    left: 50%;
    width: 50px;
    height: 50px;
    z-index: 100;
    transform: translateX(-50%);
  }
  &__nav {
    display: flex;
    align-items: center;
    width: 100%;
    font-size: 35px;
    gap: 30px;
    padding: 0 100px;
    transition: 0.8s;
    animation: 0.8s drop forwards;
    &__button {
      position: relative;
      display: flex;
      justify-content: center;
      text-decoration: none;
      width: 30%;
      font-size: 45px;
      color: var(--primary);
      transition: 0.5s;
      &:hover {
        opacity: 0.7;
      }
    }
  }
  &__info {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 100%;
    height: 100%;
    font-size: 32px;
    line-height: 40px;
    font-family: var(--medium-ff);
  }
}

.first {
  transform: translatex(-110%);
}

.second {
  transform: translateX(0);
}

@keyframes drop {
  from {
    transform: translateY(-100%);
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
