.passion {
  width: 100%;
  height: 100%;
  padding: 0 100px;
  &__row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;
    width: 100%;
  }
  &__column {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 20px;
    width: 30%;
    transition: 0.4s;
  }
  &__name {
    font-size: 32px;
    margin: 20px 0 0 0;
  }
}
